<template>
  <div class="list">
    <div class="content">
      <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">寿命预测</div>
          <span class="empty"></span>
        </div>
      </div>
      <div class="filter-search">
        <div class="filter-search-left">
          <!-- <div class="filter-select mr-12"> -->
            <DatePicker
              class="real-time-data"
              type="datetimerange"
              placeholder="起始时间                ~               截止时间"
              v-model="rangeTime"
              style="width: 350px;"
            ></DatePicker>
          <!-- </div> -->
          <div class="filter-btn" style="margin-left:20px">
            <Button type="default"
                    @click="loadTableData()"
                    :loading="pageInfo.loading">
              <span class="iconfont icon-chazhao"></span>
            </Button>
          </div>
        </div>
      </div>
      <div class="user-table">
        <Table class="m-table no-scroll no-border"
               stripe
               :loading="pageInfo.loading"
               :columns="tableColumns"
               :row-class-name="rowClassName"
               @on-row-click="onRowClick"
               :data="tableData"></Table>
      </div>
      <div class="pagination">
        <Page style=""
              @on-change="onPaginationChange"
              @on-page-size-change="onPaginationSizeChange"
              :current="pageInfo.pageIndex"
              :page-size="pageInfo.pageSize"
              :total="pageInfo.totalElements"
              show-elevator
              show-sizer
              show-total />
      </div>
    </div>
  </div>
</template>

<script>
import StatusSelect from '@/components/device-select'
import LaboratoryList from '@/components/laboratory-list'
import customDrawer from '@/components/common-drawer/common-drawer'
import {getLifePredictList} from '@/api/suanpanLink'
import moment from 'moment'
export default {
  components: {
    StatusSelect,
    LaboratoryList,
    customDrawer
  },
  data () {
    return {
      search: {
        startTime: '',
        endTime: ''
      },
      pageInfo: {
        loading: false,
        pageIndex: 1,
        pageSize: 10,
        totalElements: 30
      },
      tableData: [],
      maxlength: 50,
      selectLocation: '',
      tableColumns: [
        {
          title: '序号',
          key: 'id',
          align: 'left'
        },
        {
          title: '寿命类型',
          key: 'life_type',
          align: 'left'
        },
        {
          title: '剩余寿命',
          key: 'residual_life',
          align: 'left'
        },
        {
          title: '预测时间',
          key: 'c_time',
          align: 'left'
        }
      ],
      currentIndex: 0,
      title: '',
      type: '',
      rangeTime: ''
    }
  },
  methods: {
    selectTree (e) {
      this.formValue.addr = e
    },
    getlocation (e) {
      this.formValue.longitude = e.lng
      this.formValue.latitude = e.lat
    },
    getType () {
    },
    loadTableData (clear) {
      let {pageSize, pageIndex: pageNumber} = this.pageInfo
      this.pageInfo.loading = true
      if (clear) {
        this.pageInfo.pageIndex = 1
      }
      if (this.rangeTime && this.rangeTime[0] !== '') {
        this.search.startTime = moment(this.rangeTime[0]).format('YYYY-MM-DD HH:mm:ss')
        this.search.endTime = moment(this.rangeTime[1]).format('YYYY-MM-DD HH:mm:ss')
      }
      getLifePredictList({pageSize, pageNumber, ...this.search}).then(res => {
        let {code, data} = res
        this.pageInfo.loading = false
        if (code === 0) {
          this.tableData = data
          this.pageInfo.totalElements = res.total
        }
      })
    },
    goDetail (params) {
      this.$router.push({
        path: '/device-list-detail',
        query: {id: params.id, deviceName: params.deviceName, treeId: params.accidentTreeId || ''}
      })
    },
    onRowClick () {},
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    },
    rowClassName (row, index) {
      if (row.id == this.currentIndex) {
        return 'demo-table-info-row'
      }
    }
  },
  mounted () {
    this.loadTableData()
    this.getType()
  }
}
</script>

<style lang="less" scoped>
@import '../../../style/varient';
@size-padding: 10px;

.list {
  width: 100%;
  height: 100%;
  .content {
    width: 100%;
    height: 100%;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 24px;
  }
}
</style>



